@import './variables.scss';

html,
body,
#root {
  width: 100%;
  height: 100%;
}

html {
  box-sizing: border-box;
  width: 100vw;
  overflow-x: hidden;
}
:after,
:before {
  box-sizing: inherit;
}
li,ul,ol {
  list-style: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
.container {
  display: block;
  max-width: 1112px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
